export default {
	data() {
		return {
			PageCur: 'me',
			pages:'0',
			shadow: false,
			clickscreen: '0',
			cb:false,
			checkbox: [{
				value: 'A',
				checked: true
			}, {
				value: 'B',
				checked: false
			}, {
				value: 'C',
				checked: false
			}],
			checkboxS:[{
				value: '1',
				checked: false
			}, {
				value: '2',
				checked: true
			}, {
				value: '3',
				checked: false
			}, {
				value: '4',
				checked: true
			}, {
				value: '5',
				checked: false
			}, {
				value: '6',
				checked: true
			}]
		}
	},
	methods: {
		pageherf(index){
			if(index==0){
				this.$emit('pagechage',{pagecur:3,pagetab:3})
			}else if(index == 1){
				this.$emit('pagechage',{pagecur:6,pagetab:3})
			}else if(index == 2){
				this.$emit('pagechage',{pagecur:7,pagetab:3})
			}
		},
		NavChange: function(e) {
			let PageCur = e.currentTarget.dataset.cur


			if (PageCur == 'me') {
				uni.navigateTo({
					url: '/pages/me/me'
				});
			} else if (PageCur == 'shop') {
				uni.navigateTo({
					url: '/pages/shop/shop'
				});
			} else if (PageCur == 'movements') {
				uni.navigateTo({
					url: '/pages/movement/movement'
				});
			} else {
				uni.navigateTo({
					url: '/pages/index/index'
				});
			}

		},
	}
}
