export default {
	data() {
		return {
			screen: [],
			data: [{
				tiao: 0,
				color: 'yellow'
			}, {
				tiao: 1,
				color: '#5d9df4'
			}, {
				tiao: 2,
				color: 'rgb(3,144,218)'
			}, {
				tiao: 3,
				color: 'rgb(72,72,72)'
			}, {
				tiao: 4,
				color: 'rgb(253,118,0)'
			}, {
				tiao: 5,
				color: 'rgb(79,51,246)'
			}, {
				tiao: 6,
				color: 'rgb(191,191,191)'
			}, {
				tiao: 7,
				color: 'rgb(252,40,2)'
			}, {
				tiao: 8,
				color: 'rgb(121,10,0)'
			}, {
				tiao: 9,
				color: 'rgb(3,191,0)'
			}],
			big: 9,
			wei: 0,
			qishu: 0,
			type: 1,
			PageCur: 'shop',
		}
	},
	computed: {

	},
	watch: {
		screen() {
			console.log(this.screen)
		}
	},
	methods: {
		pageherf(url) {
			this.$emit('pagechage',{pagecur:2,pagetab:2})
		},
		typetext(it) {
			var type = this.type
			var str = type == 1 ? it : type == 2 ? (it % 2 == 1 ? '单' : '双') : (it >= 5 ? '大' : '小')
			return str
		},
		typeclass(it) {
			var type = this.type
			var str = type == 1 ? 'number' : type == 2 ? (it % 2 == 1 ? 'dan' : 'shuang') : (it >= 5 ? 'dan' : 'shuang')
			return str
		},
		moduleclick(arr, claass) {
			var s = this.screen
			// 判断是否存在数组。
			if (arr.every((item, index) => s.indexOf(item) >= 0)) {
				if (!claass) {
					for (let k in arr) {
						s.splice(s.indexOf(arr[k]), 1)
					}
				}
				return true
			} else {
				if (!claass) {
					for (let k in arr) {
						s.push(arr[k])
					}
				}
				return false
			}
		},
		NavChange: function(e) {
			let PageCur = e.currentTarget.dataset.cur


			if (PageCur == 'me') {
				uni.navigateTo({
					url: '/pages/me/me'
				});
			} else if (PageCur == 'shop') {
				uni.navigateTo({
					url: '/pages/shop/shop'
				});
			} else if (PageCur == 'movements') {
				uni.navigateTo({
					url: '/pages/movement/movement'
				});
			} else {
				uni.navigateTo({
					url: '/pages/index/index'
				});
			}

		},
	}
}
